import React, { useEffect, useState } from 'react';

// Объявляем типы для объекта window.Telegram.WebApp
declare global {
    interface Window {
        Telegram: {
            WebApp: {
                initData: string;
                initDataUnsafe: {
                    user?: {
                        id: number;
                        username?: string;
                        first_name?: string;
                        last_name?: string;
                    };
                };
                ready: () => void;
                expand: () => void;
                MainButton: {
                    show: () => void;
                    hide: () => void;
                    setText: (text: string) => void;
                    onClick: (callback: () => void) => void;
                };
                setBackgroundColor: (color: string) => void;
                setHeaderColor: (color: string) => void;
                themeParams: {
                    bg_color: string;
                    text_color: string;
                    hint_color: string;
                    button_color: string;
                    button_text_color: string;
                };
            };
        };
    }
}

export interface TelegramUserData {
    userId?: number;
    username?: string;
    firstName?: string;
    lastName?: string;
    isInitialized: boolean;
    isTelegram: boolean;
}

export const useTelegramUser = (): TelegramUserData => {
    const [userData, setUserData] = useState<TelegramUserData>({
        isInitialized: false,
        isTelegram: false
    });

    useEffect(() => {
        // Защита от ошибок при инициализации
        try {
            // Проверяем доступность Telegram API
            if (typeof window !== 'undefined' && window.Telegram && window.Telegram.WebApp) {
                // Сообщаем Telegram что приложение готово
                try {
                    window.Telegram.WebApp.ready();

                    // Раскрываем мини-приложение на всю высоту
                    window.Telegram.WebApp.expand();
                } catch (readyError) {
                    console.warn('Ошибка при вызове Telegram WebApp API методов:', readyError);
                }

                // Извлекаем данные пользователя, если они есть
                try {
                    const user = window.Telegram.WebApp.initDataUnsafe.user;

                    if (user) {
                        setUserData({
                            userId: user.id,
                            username: user.username,
                            firstName: user.first_name,
                            lastName: user.last_name,
                            isInitialized: true,
                            isTelegram: true
                        });
                    } else {
                        console.warn('Данные пользователя Telegram не найдены');
                        setUserData({
                            isInitialized: true,
                            isTelegram: true
                        });
                    }
                } catch (userError) {
                    console.error('Ошибка при получении данных пользователя Telegram:', userError);
                    setUserData({
                        isInitialized: true,
                        isTelegram: true
                    });
                }
            } else {
                // Не в среде Telegram или API недоступен
                console.warn('Telegram WebApp API не обнаружен. Приложение запущено не в Telegram.');
                setUserData({
                    isInitialized: true,
                    isTelegram: false
                });
            }
        } catch (globalError) {
            // Глобальный обработчик на случай любых непредвиденных ошибок
            console.error('Глобальная ошибка при инициализации Telegram:', globalError);
            setUserData({
                isInitialized: true,
                isTelegram: false
            });
        }
    }, []);

    return userData;
};

interface TelegramUserProps {
    showConnectionButton?: boolean;
}

export const TelegramUser: React.FC<TelegramUserProps> = ({ showConnectionButton = false }) => {
    const userData = useTelegramUser();

    useEffect(() => {
        // Защита от ошибок при работе с Telegram API
        try {
            if (userData.isTelegram && window.Telegram?.WebApp?.themeParams) {
                // Применяем стили из Telegram темы
                document.body.style.backgroundColor = window.Telegram.WebApp.themeParams.bg_color || '';
                document.body.style.color = window.Telegram.WebApp.themeParams.text_color || '';

                // Настраиваем главную кнопку если требуется
                if (showConnectionButton && window.Telegram.WebApp.MainButton) {
                    window.Telegram.WebApp.MainButton.setText('Подключить кошелек');
                    window.Telegram.WebApp.MainButton.show();
                }
            }
        } catch (error) {
            console.error('Ошибка при настройке Telegram UI:', error);
        }
    }, [showConnectionButton, userData.isTelegram]);

    if (!userData.isInitialized) {
        return <div>Загрузка данных Telegram...</div>;
    }

    if (!userData.isTelegram) {
        return <div style={{ margin: '10px 0', padding: '10px', borderRadius: '8px', backgroundColor: 'rgba(0,0,0,0.05)' }}>
            <p>Приложение запущено вне Telegram. Некоторые функции могут быть недоступны.</p>
        </div>;
    }

    return (
        <div style={{ margin: '10px 0', padding: '10px', borderRadius: '8px', backgroundColor: 'rgba(0,0,0,0.05)' }}>
            <h3>Данные пользователя Telegram</h3>
            {userData.userId ? (
                <>
                    <p>ID: {userData.userId}</p>
                    {userData.username && <p>Username: @{userData.username}</p>}
                    <p>Имя: {userData.firstName} {userData.lastName}</p>
                </>
            ) : (
                <p>Не удалось получить данные пользователя</p>
            )}
        </div>
    );
}; 