import React, { useEffect, useState, useCallback } from 'react';
import { TonConnectUIProvider, useTonAddress, useTonWallet, useTonConnectUI } from '@tonconnect/ui-react';
import { toUserFriendlyAddress } from '@tonconnect/sdk';
import './App.css';
import { TelegramUser, useTelegramUser } from './TelegramUser';
import { Welcome } from './Welcome';

// SVG иконки
const SuccessIcon = () => (
    <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M22 12C22 17.5228 17.5228 22 12 22C6.47715 22 2 17.5228 2 12C2 6.47715 6.47715 2 12 2C17.5228 2 22 6.47715 22 12Z" stroke="currentColor" strokeWidth="2" />
        <path d="M8 12L11 15L16 9" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
    </svg>
);

const ErrorIcon = () => (
    <svg viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M22 12C22 17.5228 17.5228 22 12 22C6.47715 22 2 17.5228 2 12C2 6.47715 6.47715 2 12 2C17.5228 2 22 6.47715 22 12Z" stroke="currentColor" strokeWidth="2" />
        <path d="M15 9L9 15" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
        <path d="M9 9L15 15" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
    </svg>
);

// Словарь известных публичных ключей для тестовых адресов
const knownKeys: Record<string, string> = {
    'UQDuexaUffvjGDH7eb7w4Aojp7imGQJq0gUx5vNAdIG3vNPz': 'iIydEgb9uNDqWUFsPzxzdhgKuslbVErsY7mdUUBY1hs='
};

// Функция для отправки данных на бэкенд
interface SendToBackendParams {
    walletAddress: string;
    authToken: string;
    apiUrl?: string;
    telegramInitData: string;
    tonProof: string;  // JSON строка ton_proof
}

const sendDataToBackend = async ({
    walletAddress,
    authToken,
    apiUrl = 'https://cautious-carnival-e9n2.onrender.com/api',
    telegramInitData,
    tonProof
}: SendToBackendParams): Promise<{ success: boolean; message: string }> => {
    try {
        const data = {
            walletAddress,
            tonProof
        };

        const response = await fetch(`${apiUrl}/register`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'X-Telegram-Init-Data': telegramInitData,
                'Authorization': `Bearer ${authToken}`
            },
            body: JSON.stringify(data)
        });
        const result = await response.json();

        if (!response.ok) {
            throw new Error(result.message || 'Ошибка при отправке данных');
        }

        return { success: true, message: 'Данные успешно отправлены' };
    } catch (error) {
        console.error('Ошибка при отправке данных на сервер:', error);
        return {
            success: false,
            message: error instanceof Error ? error.message : 'Неизвестная ошибка'
        };
    }
};

// Компонент для отображения сообщения об ошибке
const ErrorBoundary = ({ children }: { children: React.ReactNode }) => {
    const [hasError, setHasError] = useState(false);

    useEffect(() => {
        const handleError = () => {
            setHasError(true);
            // Здесь можно добавить отправку ошибок в мониторинг
        };

        window.addEventListener('error', handleError);
        return () => window.removeEventListener('error', handleError);
    }, []);

    if (hasError) {
        return (
            <div className="error-page">
                <h2>Что-то пошло не так</h2>
                <p>Попробуйте обновить страницу или вернуться позже.</p>
                <button className="action-button" onClick={() => window.location.reload()}>
                    Обновить страницу
                </button>
            </div>
        );
    }

    return <>{children}</>;
};

function AppContent({ userData, isTelegramApp }: { userData: any, isTelegramApp: boolean }) {
    const [tonConnectUI] = useTonConnectUI();
    const wallet = useTonWallet();
    const [tonProofStatus, setTonProofStatus] = useState<'idle' | 'loading' | 'success' | 'error'>('idle');
    const [tonProofError, setTonProofError] = useState<string | null>(null);
    const [isConnecting, setIsConnecting] = useState(false);
    const [showSuccessModal, setShowSuccessModal] = useState(false);

    // Функция для инициализации подключения кошелька с TON Proof
    const connectWalletWithTonProof = async () => {
        setTonProofStatus('loading');
        setTonProofError(null);
        setIsConnecting(true);

        try {
            // 1. Получаем payload с бэкенда
            const generateUrl = 'https://cautious-carnival-e9n2.onrender.com/api/ton-proof/generate';
            console.log('[TON Proof] Запрашиваем payload...');

            const resp = await fetch(generateUrl);
            console.log('[TON Proof] Ответ generate:', resp.status, resp.statusText);

            if (!resp.ok) {
                throw new Error(`Ошибка запроса payload: ${resp.status} ${resp.statusText}`);
            }

            const data = await resp.json();

            if (!data || !data.success || !data.payload) {
                throw new Error('Ошибка получения payload для ton_proof');
            }

            const payload = data.payload;
            console.log('[TON Proof] Получен payload:', payload);

            // 2. Устанавливаем параметры подключения с ton_proof
            console.log('[TON Proof] Устанавливаем параметры подключения с tonProof');

            // Принудительно очищаем параметры, чтобы сбросить предыдущее состояние
            tonConnectUI.setConnectRequestParameters(null);

            // Устанавливаем новые параметры с tonProof
            tonConnectUI.setConnectRequestParameters({
                state: 'ready',
                value: { tonProof: payload }
            });

            // 3. Открываем модальное окно для подключения кошелька
            console.log('[TON Proof] Открываем модальное окно подключения');
            tonConnectUI.openModal();

        } catch (e: any) {
            console.error('[TON Proof] Ошибка при подготовке подключения:', e);
            setTonProofStatus('error');
            setTonProofError(`Ошибка подключения: ${e.message || e}`);
            setIsConnecting(false);
        }
    };

    // Слушаем изменения статуса кошелька и проверяем наличие ton_proof
    useEffect(() => {
        // Переменная для отслеживания обработки проверки, чтобы избежать дублирования
        let isProcessingProof = false;

        // Функция для проверки TON Proof в подключенном кошельке
        const checkProofInWallet = async (walletInfo: any) => {
            if (!walletInfo || isProcessingProof) return;

            try {
                isProcessingProof = true;
                console.log('[TON Proof] Проверка наличия TON Proof в подключенном кошельке');

                // Проверяем, есть ли в ответе от кошелька ton_proof
                if (walletInfo.connectItems?.tonProof && 'proof' in walletInfo.connectItems.tonProof) {
                    const proof = walletInfo.connectItems.tonProof.proof;
                    console.log('[TON Proof] Найден proof:', proof);

                    // Получаем адрес кошелька в user-friendly формате
                    const userFriendlyAddress = toUserFriendlyAddress(walletInfo.account.address);
                    console.log('[TON Proof] Адрес кошелька:', userFriendlyAddress);

                    try {
                        // Отправляем proof и данные аккаунта на сервер для проверки
                        const checkUrl = 'https://cautious-carnival-e9n2.onrender.com/api/ton-proof/check';

                        // Создаем объект для отправки на сервер
                        const requestData = {
                            address: userFriendlyAddress,
                            account: walletInfo.account, // Передаем полные данные аккаунта
                            proof: proof // Передаем proof как есть, без модификаций
                        };

                        console.log('[TON Proof] Отправляем данные для проверки:', JSON.stringify(requestData));

                        // Отправляем запрос на сервер
                        const verifyResp = await fetch(checkUrl, {
                            method: 'POST',
                            headers: {
                                'Content-Type': 'application/json',
                                'Accept': 'application/json'
                            },
                            body: JSON.stringify(requestData)
                        });

                        console.log('[TON Proof] Статус ответа:', verifyResp.status);

                        // Обрабатываем ответ
                        const responseData = await verifyResp.json();
                        console.log('[TON Proof] Разобранный ответ:', responseData);

                        if (verifyResp.ok && responseData && responseData.success && responseData.token) {
                            localStorage.setItem('auth_token', responseData.token);

                            // --- Автоматическая регистрация пользователя ---
                            try {
                                const backendResult = await sendDataToBackend({
                                    walletAddress: userFriendlyAddress,
                                    authToken: responseData.token,
                                    telegramInitData: (typeof window !== 'undefined' && window.Telegram?.WebApp?.initData) || '',
                                    tonProof: JSON.stringify(proof)
                                });
                                if (!backendResult.success) {
                                    console.error('[Register] Ошибка регистрации:', backendResult.message);
                                } else {
                                    console.log('[Register] Пользователь успешно зарегистрирован');
                                }
                            } catch (regErr) {
                                console.error('[Register] Исключение при регистрации:', regErr);
                            }

                            setTonProofStatus('success');
                            setShowSuccessModal(true);
                            console.log('[TON Proof] Успешная авторизация!');
                        } else {
                            setTonProofStatus('error');
                            setTonProofError(responseData && responseData.message ? responseData.message : 'Ошибка при проверке подключения кошелька');
                            setIsConnecting(false);
                        }
                    } catch (e: any) {
                        console.error('[TON Proof] Ошибка при проверке:', e);
                        setTonProofStatus('error');
                        setTonProofError(`Ошибка при подключении кошелька: ${e.message || String(e)}`);
                        setIsConnecting(false);
                    }
                } else {
                    console.log('[TON Proof] Подпись не найдена в подключенном кошельке');
                    setTonProofStatus('error');
                    setTonProofError('Не удалось подключить кошелек. Возможно, вы отклонили запрос на подпись.');
                    setIsConnecting(false);
                }
            } finally {
                isProcessingProof = false;
            }
        };

        // Если кошелек подключен и мы находимся в процессе подключения - проверяем наличие TON Proof
        if (wallet && isConnecting) {
            checkProofInWallet(wallet);
        }
    }, [wallet, isConnecting]);

    // Модальное окно для статуса подключения
    const ConnectionSuccessModal = () => {
        return (
            <div className={`modal-overlay ${showSuccessModal ? 'visible' : ''}`}>
                <div className="modal-content">
                    <div className="result-icon success" style={{ margin: '0 auto' }}>
                        <SuccessIcon />
                    </div>
                    <h3 className="modal-title">Успешно!</h3>
                    <p>Ваш кошелек TON успешно подключен к аккаунту Telegram.</p>
                    <p>Теперь вы можете закрыть приложение.</p>
                    <button className="action-button" onClick={() => setShowSuccessModal(false)}>Готово</button>
                </div>
            </div>
        );
    };

    return (
        <div className={`App ${isTelegramApp ? 'tma-app' : ''}`}>
            <header className="app-header">
                <div className="app-title">
                    {isTelegramApp ? 'TON + Telegram' : 'TON Connect'}
                </div>
            </header>
            <main>
                <Welcome
                    username={userData.username}
                    isConnected={wallet !== null && tonProofStatus === 'success'}
                />

                {!wallet && (
                    <div className="card">
                        <p>Для использования приложения необходимо подключить ваш кошелек TON.</p>
                        <button
                            className="action-button"
                            onClick={connectWalletWithTonProof}
                            disabled={tonProofStatus === 'loading'}
                        >
                            {tonProofStatus === 'loading' ? 'Подключение...' : 'Подключить кошелек'}
                        </button>
                    </div>
                )}

                {tonProofStatus === 'loading' && <div className="mini-spinner">Подключение кошелька...</div>}
                {tonProofStatus === 'error' && <div className="error">{tonProofError}</div>}

                {/* Модальное окно при успешном подключении */}
                <ConnectionSuccessModal />
            </main>
        </div>
    );
}

function App() {
    // Используем внешний URL манифеста
    const manifestUrl = 'https://srpvpn.github.io/literate-bassoon/tonconnect-manifest.json';
    const userData = useTelegramUser();
    const [isTelegramApp, setIsTelegramApp] = useState<boolean>(false);
    const [hasError, setHasError] = useState(false);
    const [isReady, setIsReady] = useState(false);

    useEffect(() => {
        // Проверяем, запущено ли приложение в TMA
        try {
            const isTMA = typeof window !== 'undefined' && !!window.Telegram?.WebApp;
            setIsTelegramApp(isTMA);
            if (isTMA && window.Telegram.WebApp) {
                document.body.classList.add('tma-theme');
            }
            setTimeout(() => {
                setIsReady(true);
            }, 1000);
        } catch (error) {
            console.error('Ошибка при проверке TMA:', error);
            setHasError(true);
        }
    }, []);

    if (hasError) {
        return (
            <div className="App error-page">
                <h2>Что-то пошло не так</h2>
                <p>Не удалось загрузить приложение. Пожалуйста, попробуйте позже.</p>
                <button className="action-button" onClick={() => window.location.reload()}>
                    Обновить страницу
                </button>
            </div>
        );
    }
    if (!isReady) {
        return (
            <div className="loading">
                <div className="spinner"></div>
                <p>Инициализация приложения...</p>
            </div>
        );
    }
    return (
        <ErrorBoundary>
            <TonConnectUIProvider manifestUrl={manifestUrl}>
                <AppContent userData={userData} isTelegramApp={isTelegramApp} />
            </TonConnectUIProvider>
        </ErrorBoundary>
    );
}

export default App; 