import React from 'react';

// Компонент для отображения информации о кошельке
interface WelcomeProps {
    username?: string;
    isConnected: boolean;
}

export const Welcome: React.FC<WelcomeProps> = ({ username, isConnected }) => {
    return (
        <div className="welcome-card card">
            <h1 className="welcome-title">
                {username ? `Привет, ${username}!` : 'Добро пожаловать!'}
            </h1>
            <p className="welcome-subtitle">
                {isConnected
                    ? 'Ваш кошелек TON успешно подключен'
                    : 'Подключите ваш кошелек TON для привязки к Telegram аккаунту'}
            </p>
            {!isConnected && (
                <p>Нажмите на кнопку "Подключить кошелек" ниже.</p>
            )}
        </div>
    );
}; 